@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
* {
  box-sizing: border-box;
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  color: white;
}

body {
  margin: 0;
}

.container-main{display:flex;position:absolute;overflow:hidden;background-color:#12181b}
.flex-center{display:flex;justify-content:center;align-items:center}.flex-space-between{display:flex;justify-content:space-between;align-items:center;background-color:inherit}.flex-wrap{flex-wrap:wrap}.container-full-screen{width:100vw;height:100vh}.dark-bg{background-color:#2a2e35}.mg-top-30{margin-top:30px}.pd-10{padding:10px}.pd-20{padding:20px}.btn{width:auto;height:auto;padding:6px 16px;border-radius:5px;display:flex;justify-content:center;align-items:center;letter-spacing:1px;background-color:#43a047;color:#fff;outline:none;border:none;cursor:pointer;box-shadow:1px 1px 1px 1px #3d3c3d}.btn--neutral{background-color:#607d8b}.size-auto{width:auto;height:auto}.form-link{margin-top:15px;margin-left:auto;text-decoration:none;letter-spacing:2px}.form-error{display:inline-block;width:100%;text-align:center;color:crimson;font-size:.8rem;letter-spacing:1px}.form{width:100%;height:auto}@media screen and (min-width: 600px){.form{width:580px;display:flex;flex-wrap:wrap}}.form fieldset{border:1px dotted rgba(200,200,200,.2);border-radius:10px}.form legend{font-size:50px;letter-spacing:5px;font-family:"Indie Flower",cursive;color:#558b2f}.form input{width:100%;height:auto;padding:8px 3px;border:1px solid rgba(200,200,200,.4);outline:none;letter-spacing:1px;margin-bottom:15px;border-radius:5px;font-size:20px;color:#c8c8c8}.form button{padding:8px 18px;font-size:1rem;letter-spacing:1px;background-color:#558b2f;color:#fff;border-radius:100px;outline:none;border:none;margin-left:auto;margin-top:10px;cursor:pointer}
#container-board{flex:1 1;height:100%;display:flex;justify-content:center;align-items:center}#board{border:1px solid rgba(200,200,200,.4);display:flex;flex-wrap:wrap}
.cell{height:40px;width:40px;border:1px solid rgba(200,200,200,.2);cursor:pointer;outline:none;color:#fff;font-size:16px;font-family:"Roboto",sans-serif}
.sidebar{height:100vh;width:90px;display:flex;flex-direction:column;align-items:flex-start;background-color:#2a2e35;box-shadow:-5px 0px 10px -3px rgba(0,0,0,.6)}.container-icon{width:100%;height:auto;cursor:pointer;border-bottom:1px solid rgba(200,200,200,.4)}.container-icon button{width:auto;height:auto;outline:none;border:none;background-color:transparent;cursor:pointer}.container-icon span{font-size:.8rem;margin-top:2px}.container-settings{width:auto;height:auto;border-radius:10px;background-color:#fff}.container-settings label{font-size:1rem;letter-spacing:2px;text-transform:uppercase;color:#000;background-color:#fff;font-weight:700;display:block}.container-settings label:not(:first-child){padding-left:100px}.container-settings input{display:block;width:auto;height:auto;border:2px solid #000;border-radius:5px;color:#000;background-color:#fff;font-weight:700;padding:5px 3px;font-size:1rem;letter-spacing:1px}.container-space-between{width:100%;height:auto;display:flex;justify-content:space-between;align-items:center;flex-wrap:wrap;padding:20px 20px;border-bottom:1px dashed rgba(200,200,200,.4)}.button-reset{padding:8px 18px;background:transparent;border:1px solid rgba(200,200,200,.4);outline:none;border-radius:100px;cursor:pointer}.board-size-input{width:60px;height:auto;padding:5px 3px;border-radius:2px;border:1px solid rgba(200,200,200,.4);background-color:transparent;color:#fff;outline:none;display:block;margin-right:20px}.board-size-label{font-size:12px;letter-spacing:1px;display:block}.number-of-bombs-label{font-size:12px;letter-spacing:1px;display:block}.number-of-bombs-input{width:60px;height:auto;padding:5px 3px;border-radius:2px;border:1px solid rgba(200,200,200,.4);background-color:rgba(0,0,0,0);color:#fff;outline:none;display:block;margin-right:20px}.hamburger{width:40px;height:auto;background-color:rgba(0,0,0,0);outline:none;border:none;cursor:pointer;padding:0px}.hamburger__line{width:100%;height:4px;background-color:#fff;border-radius:100px;margin-bottom:5px;display:block}#button-submit-board-settings{min-width:80px;min-height:20px;width:auto;height:auto;padding:8px 18px;font-size:16px;background-color:#558b2f;display:flex;justify-content:flex-start;align-items:center;color:#fff;font-size:16px;letter-spacing:1px;border-radius:100px;outline:none;border:none;margin-left:auto;margin-top:30px;cursor:pointer}.icon{color:green}
.container-modal{width:100%;height:100vh;background-color:rgba(0,0,0,.4);position:absolute;top:0;left:0}
.settings-title{background-color:inherit;color:#000;margin:0px 0px 20px 0px}
